ion__form-button_show{cursor:pointer;position:absolute;border:none;width:24px;height:24px;top:16px;right:16px;background-color:#f9f9fa}.identification__form-successful_—Åheck{position:absolute;width:24px;height:24px;top:17px;right:42px}.identification__transition{margin-top:16px;display:flex;align-items:center}.identification__transition-title{font-weight:400;font-size:16px;line-height:24px;color:#727272;margin-right:16px;text-align:center}.identification__transition-link{font-weight:500;font-size:12px;line-height:18px;color:#363636;display:flex;align-items:center;font-weight:600;text-transform:uppercase;transition:all .2ms}.identification__transition-link:hover{color:#f83600}.identification__transition-icon{margin-left:15px;width:18px;height:12px}.identification input[type=submit]::active,.identification :hover,.identification :focus{outline:0;outline-offset:0}.identification button::active,.identification :hover,.identification :focus{border:none;outline:0;outline-offset:0}